<template>
  <div class="shopCart">
    <div class="section">
      <div class="location">
        <span></span> <a href="/index.html"></a> &gt;
        <a href="/cart.html"></a>
      </div>
    </div>

    <div class="section">
      <div class="wrapper">
        <div class="bg-wrap">
          <!--  -->
          <div class="cart-head clearfix">
            <h2><i class="iconfont icon-cart"></i></h2>
            <div class="cart-setp">
              <ul>
                <li class="first active">
                  <div class="progress"><span>1</span> </div>
                </li>
                <li>
                  <div class="progress"><span>2</span> </div>
                </li>
                <li class="last">
                  <div class="progress"><span>3</span> /</div>
                </li>
              </ul>
            </div>
          </div>
          <!--  -->
          <!--  -->
          <div class="cart-box">
            <input
              id="jsondata"
              name="jsondata"
              type="hidden"
            />
            <table
              width="100%"
              align="center"
              class="cart-table"
              border="0"
              cellspacing="0"
              cellpadding="8"
            >
              <tbody>
                <tr>
                  <th
                    width="48"
                    align="center"
                  ><a></a></th>
                  <th align="left"></th>
                  <th
                    width="84"
                    align="left"
                  ></th>
                  <th
                    width="104"
                    align="center"
                  ></th>
                  <th
                    width="104"
                    align="left"
                  >()</th>
                  <th
                    width="54"
                    align="center"
                  ></th>
                </tr>
                <!--  -->
                <tr v-for="(item, index) in goodsList" :key="item.id">
                  <td>
                    <el-switch
                      v-model="item.isSelected"
                      active-color="#13ce66"
                      inactive-color="9e9e9e"
                    >
                    </el-switch>
                  </td>
                  <td><img
                      :src="item.img_url"
                      alt=""
                    >
                    <span>
                      {{item.title}}
                    </span>
                  </td>
                  <td>{{item.sell_price}}</td>
                  <td>
                    <el-input-number
                      v-model="item.buycount"
                      :min="0"
                      size='mini'
                    >
                    </el-input-number>
                  </td>
                  <td>{{item.buycount*item.sell_price}}</td>
                  <td>
                    <!--  -->
                    <el-button
                      @click='delOne(item.id)'
                      type='danger'
                      icon='el-icon-delete'
                      circle
                    ></el-button>
                  </td>
                </tr>
                <!--  -->
                <tr v-show="goodsList.length == 0 ">
                  <td colspan="10">
                    <div class="msg-tips">
                      <div class="icon warning">
                        <i class="iconfont icon-tip"></i>
                      </div>
                      <div class="info">
                        <strong></strong>
                        <p>
                          
                          <a href="/index.html"></a>
                        </p>
                      </div>
                    </div>
                  </td>
                </tr>
                <tr>
                  <th
                    align="right"
                    colspan="8"
                  >
                     <b
                      class="red"
                      id="totalQuantity"
                    >{{selectedCount}}</b> 
                    &nbsp;&nbsp;&nbsp; 
                    <span class="red"></span>
                    <b
                      class="red"
                      id="totalAmount"
                    >{{selectPrice}}</b>
                  </th>
                </tr>
              </tbody>
            </table>
          </div>
          <!-- / -->
          <!--  -->
          <div class="cart-foot clearfix">
            <div class="right-box">
              <button
                class="button"
                onclick="javascript:location.href='/index.html';"
              >
                
              </button>
              <!-- 
               
                1. routelink 
                2.selectedids
                  watch
                    watchgoodlist deep
                    selectedIds
                    
                       selectedids -->
              <router-link :to="'/order/'+selectedids">
                <button class="submit">
                  
                </button>
              </router-link>
            </div>
          </div>
          <!--  -->
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "shopCart",
  data: function() {
    return {
      //   
      goodsList: []
    };
  },
  //   
  methods: {
    delOne(id) {
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
    })
    .then(() => {
        // 
        // 
        this.goodsList.forEach((v,index)=> {        
          if(v.id==id) {
            this.goodsList.splice(index,1)
          }
        })
        // Vuex
        // watch   Vuex
        // this.$store.commit('delGoodsById',id);
        // 
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
    }
  },
  // 
  computed: {
    //   
    selectedCount() {
      let num = 0;
      this.goodsList.forEach(element => {
        //   
        if (element.isSelected == true) {
          // 
          num += element.buycount;
        }
      });
      return num;
    },
    // 
    selectPrice() {
      let price = 0;
      this.goodsList.forEach(element => {
        if (element.isSelected == true) {
          price += element.buycount * element.sell_price;
        }
      });
      return price;
    },
    // id
    selectedids() {
      let ids = "";
      this.goodsList.forEach(v => {
        if (v.isSelected == true) {
          ids += v.id;
          ids += ",";
        }
      });
      // 
      ids = ids.slice(0, ids.length - 1);
      // ids
      return ids;
    }
  },
  // 
  created() {
    // id1,id2,id3
    let ids = "";
    // vuex
    for (const key in this.$store.state.cartData) {
      ids += key;
      ids += ",";
    }
    // 
    // //console.log(ids.slice(0,ids.length-1));
    ids = ids.slice(0, ids.length - 1);
    // 
    this.$axios.get(`site/comment/getshopcargoods/${ids}`).then(result => {
      // //console.log(result);
      // return;
      //  
      result.data.message.forEach(v => {
        v.buycount = this.$store.state.cartData[v.id];
        //   switch 
        v.isSelected = true;
      });
      // //console.log(result);
      this.goodsList = result.data.message;
    });
  },
  // watch
  watch: {
    goodsList: {
      //  
      handler: function(val, oldVal) {
        // //console.log(val);
        // //console.log(oldVal);

        let obj = {};
        val.forEach(v => {
          obj[v.id] = v.buycount;
        });
        // //console.log(obj);
        // Vuex
        this.$store.commit("updateCartData", obj);
      },
      //  
      deep: true
    }
  }
};
</script>
<style>
td img {
  width: 100px;
}
td span {
  margin-left: 10px;
}
td:nth-child(2) {
  display: flex;
  align-items: center;
}
</style>

