<template>
  <div class="login">
    <div class="section">
      <div class="location">
        <span></span>
        <a href="/index.html"></a> &gt;
        <a href="/login.html"></a>
      </div>
    </div>
    <div class="section">
      <div class="wrapper">
        <div class="bg-wrap">
          <div class="nav-tit">
            <a
              class="selected"
              href="javascript:;"
            ></a>
            <i>|</i>
            <a href="/register.html"></a>
          </div>

          <div
            id="loginform"
            name="loginform"
            class="login-box"
          >
            <div class="input-box">
              <input
                id="txtUserName"
                name="txtUserName"
                type="text"
                placeholder="//"
                maxlength="50"
                v-model.trim="user_name"
              >
            </div>
            <div class="input-box">
              <input
                id="txtPassword"
                name="txtPassword"
                type="password"
                placeholder=""
                maxlength="16"
                v-model.trim="password"
              >
            </div>
            <div class="btn-box">
              <input
                id="btnSubmit"
                name="btnSubmit"
                type="submit"
                value=""
                @click="login"
              >
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "login",
  data: function() {
    return {
      user_name: "",
      password: ""
    };
  },
  methods: {
    // 
    login() {
      // 
      if (this.user_name == "" || this.password == "") {
        this.$Message.warning("");
      } else {
        // 
        this.$axios
          .post(`site/account/login`, {
            user_name: this.user_name,
            password: this.password
          })
          .then(result => {
            // //console.log(result);
            // 
            if (result.data.status === 0) {
              this.$Message.success(result.data.message);
              // Vuex
              this.$store.commit("changeLogin", true);
              // 
              this.$router.go(-1);
            } else {
              // 
              this.$Message.error(result.data.message);
            }
          });
      }
    }
  }
};
</script>
