<template>
  <div>
    <div class="section">
      <div class="location">
        <span></span>
        <a href="/index.html"></a> &gt;
        <a href="/cart.html"></a>
      </div>
    </div>

    <div class="section">
      <div class="wrapper">
        <div class="bg-wrap">
          <!---->
          <div class="cart-head clearfix">
            <h2>
              <i class="iconfont icon-cart"></i></h2>
            <div class="cart-setp">
              <ul>
                <li class="first active">
                  <div class="progress">
                    <span>1</span>
                    
                  </div>
                </li>
                <li class="active">
                  <div class="progress">
                    <span>2</span>
                    
                  </div>
                </li>
                <li class="last">
                  <div class="progress">
                    <span>3</span>
                    /
                  </div>
                </li>
              </ul>
            </div>
          </div>
          <!---->
          <div class="cart-box">
            <h2 class="slide-tit">
              <span>1</span>
            </h2>
            <div
              id="orderForm"
              name="orderForm"
              url=""
            >
              <!-- element-uiform -->
              <el-form
                :model="ruleForm"
                :rules="rules"
                ref="ruleForm"
                label-width="100px"
                class="demo-ruleForm"
              >

                <el-form-item
                  class="My-form"
                  label=""
                  prop="accept_name"
                >
                  <el-input v-model="ruleForm.accept_name"></el-input>
                </el-form-item>

                <el-form-item
                  label=""
                  prop="area"
                >
                  <VDistpicker
                    @selected="selectedArea"
                    :province="ruleForm.area.province.value"
                    :city="ruleForm.area.city.value"
                    :area="ruleForm.area.area.value"
                  ></VDistpicker>
                </el-form-item>

                <el-form-item
                  class="My-form"
                  label=""
                  prop="address"
                >
                  <el-input v-model="ruleForm.address"></el-input>
                </el-form-item>
                <el-form-item
                  class="My-form"
                  label=""
                  prop="mobile"
                >
                  <el-input v-model="ruleForm.mobile"></el-input>
                </el-form-item>
                <el-form-item
                  class="My-form"
                  label=""
                  prop="email"
                >
                  <el-input v-model="ruleForm.email"></el-input>
                </el-form-item>
                <el-form-item
                  class="My-form-main"
                  label=""
                  prop="post_code"
                >
                  <el-input v-model="ruleForm.post_code"></el-input>
                </el-form-item>

                <h2 class="slide-tit">
                  <span>2</span>
                </h2>
                <ul class="item-box clearfix">
                  <!--DataTable-->
                  <li>
                    <label>
                      <!-- <input
                        name="payment_id"
                        type="radio"
                        onclick="paymentAmountTotal(this);"
                        value="1"
                      >
                      <input
                        name="payment_price"
                        type="hidden"
                        value="0.00"
                      >-->
                      <el-radio
                        v-model="ruleForm.payment_id"
                        :label="6"
                      > </el-radio>
                      &nbsp<em>0.00</em>
                    </label>
                  </li>
                </ul>
                <h2 class="slide-tit">
                  <span>3</span>
                </h2>
                <ul class="item-box clearfix">
                  <!--DataTable-->
                  <li>
                    <label>
                      <!-- <input
                        name="express_id"
                        type="radio"
                        onclick="freightAmountTotal(this);"
                        value="1"
                        datatype="*"
                        sucmsg=" "
                      >
                      <input
                        name="express_price"
                        type="hidden"
                        value="20.00"
                      > -->
                      <el-radio
                        v-model="ruleForm.express_id"
                        @change='ruleForm.expressMoment=20.00'
                        label="1"
                      ></el-radio>
                      &nbsp&nbsp<em>20.00</em>&nbsp&nbsp&nbsp&nbsp

                      <el-radio
                        v-model="ruleForm.express_id"
                        @change='ruleForm.expressMoment=10.00'
                        label="2"
                      ></el-radio>
                      &nbsp&nbsp<em>10.00</em>&nbsp&nbsp&nbsp&nbsp

                      <el-radio
                        v-model="ruleForm.express_id"
                        @change='ruleForm.expressMoment=8.00'
                        label="3"
                      ></el-radio>
                      &nbsp&nbsp<em>8.00</em>

                      <span class="Validform_checktip"></span>
                    </label>
                  </li>
                </ul>
                <h2 class="slide-tit">
                  <span>4</span>
                </h2>
                <div class="line15"></div>
                <table
                  width="100%"
                  border="0"
                  align="center"
                  cellpadding="8"
                  cellspacing="0"
                  class="cart-table"
                >
                  <tbody>
                    <tr>
                      <th
                        colspan="2"
                        align="left"
                      ></th>
                      <th
                        width="84"
                        align="left"
                      ></th>
                      <th
                        width="84"
                        align="center"
                      ></th>
                      <th
                        width="104"
                        align="left"
                      >()</th>
                    </tr>
                    <!--  -->
                    <tr
                      v-for="(item, index) in goodsList"
                      :key="item.id"
                    >
                      <td width="68">
                        <!-- <a   target="_blank"
                      href="/goods/show-89.html"
                    >-->
                        <router-link :to="'/detail/'+item.id">
                          <img
                            :src="item.img_url"
                            class="img"
                          >
                        </router-link>

                        <!-- </a> -->
                      </td>
                      <td>
                        <!-- <a target="_blank" href="/goods/show-89.html" > -->
                        <router-link :to="'/detail/'+item.id">
                          {{item.title}}
                        </router-link>
                        <!-- </a> -->
                      </td>
                      <td>
                        <span class="red">
                          {{ item.sell_price }}
                        </span>
                      </td>
                      <td align="center">{{ item.buycount }}</td>
                      <td>
                        <span class="red">
                          {{ item.buycount*item.sell_price }}
                        </span>
                      </td>
                    </tr>
                  </tbody>
                </table>
                <div class="line15"></div>
                <h2 class="slide-tit">
                  <span>5</span>
                </h2>
                <div class="buy-foot clearfix">
                  <div class="left-box">
                    <dl>
                      <dt>(100)</dt>
                      <dd>
                        <textarea
                          name="message"
                          class="input"
                          style="height:35px;"
                          v-model="ruleForm.message"
                        ></textarea>
                      </dd>
                    </dl>
                  </div>
                  <div class="right-box">
                    <p>
                      
                      <label class="price">{{ totalCount }}</label> &nbsp;&nbsp;&nbsp;&nbsp; 
                      <label
                        id="goodsAmount"
                        class="price"
                      >{{ totalPrice }}.00</label> &nbsp;&nbsp;&nbsp;&nbsp;
                    </p>
                    <p>
                      
                      <label
                        id="expressFee"
                        class="price"
                      >{{ruleForm.expressMoment}}.00</label> 
                    </p>
                    <p class="txt-box">
                      
                      <label
                        id="totalAmount"
                        class="price"
                      >{{totalPrice+ruleForm.expressMoment}}.00</label>
                    </p>
                    <p class="btn-box">
                      <!-- <a
                        class="btn button"
                        href="/cart.html"
                      ></a> -->
                      <router-link
                        class="btn button"
                        to="/shopCart"
                      ></router-link>
                      <a
                        id="btnSubmit"
                        class="btn submit"
                        @click="submit('ruleForm')"
                      ></a>
                    </p>
                  </div>
                </div>
              </el-form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// 
import VDistpicker from "v-distpicker";

export default {
  name: "order",
  data: function() {
    //   element-ui
    var validateMobile = (rule, value, callback) => {
      //  //console.log(value)
      // 
      if (value === "") {
        callback(new Error(""));
      } else {
        //  
        // 
        let reg = /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/;
        // 
        if (reg.test(value) == true) {
          callback();
        } else {
          // 
          callback(new Error(""));
        }
      }
    };
    //   element-ui
    var validateEmail = (rule, value, callback) => {
      //  //console.log(value)
      // 
      if (value === "") {
        callback(new Error(""));
      } else {
        //  
        // 
        let reg = /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/;
        // 
        if (reg.test(value) == true) {
          callback();
        } else {
          // 
          callback(new Error(""));
        }
      }
    };
    //   element-ui
    var validatePostCode = (rule, value, callback) => {
      //  //console.log(value)
      // 
      if (value === "") {
        callback(new Error(""));
      } else {
        //  
        // 
        let reg = /^[1-9]\d{5}(?!\d)$/;
        // 
        if (reg.test(value) == true) {
          callback();
        } else {
          // 
          callback(new Error(""));
        }
      }
    };

    return {
      goodsList: [],
      // 
      totalCount: 0,
      // ()
      totalPrice: 0,
      // element-ui
      ruleForm: {
        accept_name: "",
        address: "313",
        mobile: "13456786666",
        email: "5433460@qq.com",
        post_code: "440306",
        area: {
          province: {
            code: "440000",
            value: ""
          },
          city: {
            code: "440300",
            value: ""
          },
          area: {
            code: "440306",
            value: ""
          }
        },
        payment_id: 6,
        express_id: "1",
        expressMoment: 20,
        message: ""
      },
      rules: {
        accept_name: [
          { required: true, message: "", trigger: "change" },
          {
            min: 2,
            max: 10,
            message: " 2  10 ",
            trigger: "change"
          }
        ],
        address: [
          { required: true, message: "", trigger: "change" },
          { min: 2, message: "", trigger: "change" }
        ],
        mobile: [{ validator: validateMobile, trigger: "change" }],
        email: [{ validator: validateEmail, trigger: "change" }],
        post_code: [{ validator: validatePostCode, trigger: "change" }]
      }
    };
  },
  methods: {
    selectedArea(newArea) {
      // //console.log(newArea);
      this.ruleForm.area = newArea;
    },
    submit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.ruleForm.goodsAmount = this.totalPrice;
          this.ruleForm.goodsids = this.ids;
          let obj = {};
          this.goodsList.forEach(v => {
            obj[v.id] = v.buycount;
          });
          this.ruleForm.cargoodsobj = obj;
          this.$axios
            .post("site/validate/order/setorder", this.ruleForm)
            .then(result => {
              //console.log(result);
              this.$Message.success(",!");
              this.$router.push("/payMoney/"+result.data.message.orderid);
              this.goodsList.forEach(v=> {
                this.$store.commit('delGoodsById',v.id);
              })
            });
        } else {
          this.$Message.warning(",");
          return false;
        }
      });
    }
  },
  // id
  created() {
    // 
    // //console.log(this.$route);
    //  
    //console.log(this.$route);
    this.ids = this.$route.params.ids;
    // 
    this.$axios
      .get(`site/validate/order/getgoodslist/${this.ids}`)
      .then(result => {
        //console.log(result);
        // buycount 
        this.goodsList = result.data.message;
        // buycount
        result.data.message.forEach(v => {
          //console.log(v);

          // Vuex
          // buycountVue.set
          //  
          v.buycount = this.$store.state.cartData[v.id];
          // 
          this.totalCount += v.buycount;
          // 
          this.totalPrice += v.buycount * v.sell_price;
        });
        // //console.log(this.goodsList);
        // //console.log(result);
      });
  },
  // 
  components: { VDistpicker }
  //   created() {created   
  //   beforeCreate() { // created   
  // // 
  // this.$axios.get("site/account/islogin").then(result => {
  //   //   //console.log(result);
  //   if (result.data.code == "nologin") {
  //     // 
  //     this.$Message.warning("");
  //     // ()
  //     setTimeout(() => {
  //       this.$router.push("/index");
  //     }, 1000);
  //   }
  // });
  //   }
};
</script>
<style>
.My-form .el-input__inner {
  width: 500px;
}
.My-form-main .el-input__inner {
  width: 100px;
}
</style>