<template>
  <div>
    <div class="section">
      <div class="location">
        <span></span>
        <a href="/index.html"></a> &gt;
        <a href="/goods.html"></a> &gt;
        <a href="/goods/42/1.html"></a>
      </div>
    </div>
    <div class="section">
      <div class="wrapper clearfix">
        <div class="wrap-box">
          <div class="left-925">
            <div class="goods-box clearfix">
              <!--  -->
              <div class="pic-box">
                <ProductZoomer  
                    v-if="images.normal_size.length!=0" 
                    :base-images="images"
                    :base-zoomer-options="zoomerOptions"
                 />
              </div>
              <div class="goods-spec">
                <h1>{{ goodsinfo.title }}</h1>
                <p class="subtitle">{{ goodsinfo.sub_title }}</p>
                <div class="spec-box">
                  <dl>
                    <dt></dt>
                    <dd id="commodityGoodsNo">{{ goodsinfo.goods_no }}</dd>
                  </dl>
                  <dl>
                    <dt></dt>
                    <dd>
                      <s id="commodityMarketPrice">{{ goodsinfo.market_price }}</s>
                    </dd>
                  </dl>
                  <dl>
                    <dt></dt>
                    <dd>
                      <em id="commoditySellPrice" class="price">{{ goodsinfo.sell_price }}</em>
                    </dd>
                  </dl>
                </div>
                <div class="spec-box">
                  <dl>
                    <dt></dt>
                    <dd>
                      <div class="stock-box">
                        <el-input-number v-model="buyCount" :min="1" :max=" goodsinfo.stock_quantity "></el-input-number>
                      </div>
                      <span class="stock-txt">
                        
                        <em id="commodityStockNum">{{ goodsinfo.stock_quantity }}</em>
                      </span>
                    </dd>
                  </dl>
                  <dl>
                    <dd>
                      <div id="buyButton" class="btn-buy">
                        <!-- <button onclick="cartAdd(this,'/',1,'/shopping.html');" class="buy"></button> -->
                        <router-link to="/order/:artID">
                             <button  class="buy">
                          </button>
                          </router-link>
                        <button @click="add2Cart" class="add"></button>
                      </div>
                    </dd>
                  </dl>
                </div>
              </div>
            </div>
            <div id="goodsTabs" class="goods-tab bg-wrap">
              <!-- iView   -->
              <Affix>
                <div id="tabHead" class="tab-head" style="position: static; top: 517px; width: 925px;">
                  <ul>
                    <li>
                      <a href="javascript:;" @click="tabIndex=0" :class="{selected:tabIndex==0}"></a>
                    </li>
                    <li>
                      <a href="javascript:;" @click="tabIndex=1" :class="{selected:tabIndex==1}"></a>
                    </li>
                  </ul>
                </div>
              </Affix>
              <!-- html -->
              <div class="tab-content entry" v-show="tabIndex==0" v-html="goodsinfo.content">

              </div>
              <div class="tab-content" v-show="tabIndex==1">
                <div class="comment-box">
                  <div id="commentForm" name="commentForm" class="form-box">
                    <div class="avatar-box">
                      <i class="iconfont icon-user-full"></i>
                    </div>
                    <div class="conn-box">
                      <div class="editor">
                        <textarea v-model.trim="comment" id="txtContent" name="txtContent" sucmsg=" " datatype="*10-1000" nullmsg=""></textarea>
                        <span class="Validform_checktip"></span>
                      </div>
                      <div class="subcon">
                        <input @click="submitComment" id="btnSubmit" name="submit" type="submit" value="" class="submit">
                        <span class="Validform_checktip"></span>
                      </div>
                    </div>
                  </div>
                  <ul id="commentList" class="list-box">
                    <p v-show="totalcount==0" style="margin: 5px 0px 15px 69px; line-height: 42px; text-align: center; border: 1px solid rgb(247, 247, 247);"></p>
                    <li v-for="(item, index) in comments" :key="item.id">
                      <div class="avatar-box">
                        <i class="iconfont icon-user-full"></i>
                      </div>
                      <div class="inner-box">
                        <div class="info">
                          <span>{{ item.user_name }}</span>
                          <span>{{ item.reply_time | shortTimePlus }}</span>
                        </div>
                        <p>{{ item.content }}</p>
                      </div>
                    </li>

                  </ul>
                  <div class="page-box" style="margin: 5px 0px 0px 62px;">
                    <div id="pagination" class="digg">
                      <!-- iView -->
                      <Page :current='pageIndex' @on-change='pageChange' :total="totalcount" show-sizer show-elevator placement='top' :page-size-opts='[15, 30, 45, 60]' :page-size='pageSize' />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="left-220">
            <div class="bg-wrap nobg">
              <div class="sidebar-box">
                <h4></h4>
                <ul class="side-img-list">
                  <li v-for="(item, index) in hotgoodslist" :key="item.id">
                    <div class="img-box">
                      <!-- <a href="#/site/goodsinfo/90" class=""> -->
                      <router-link :to="'/detail/'+item.id">
                        <img :src="item.img_url">
                      </router-link>
                      <!-- </a> -->

                    </div>
                    <div class="txt-box">
                      <!-- <a href="#/site/goodsinfo/90" class=""> -->
                      <router-link :to="'/detail/'+item.id">
                        {{ item.title }}
                      </router-link>
                      <!-- </a> -->
                      <span>{{ item.add_time | shortTime}}</span>
                    </div>
                  </li>

                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <BackTop></BackTop>
  </div>
</template>

<script>
// jquery
import $ from "jquery";
// 
export default {
  name: "detail",
  //   
  data() {
    return {
      // id
      artID: "",
      // 
      goodsinfo: [],
      // 
      imglist: [],
      // 
      hotgoodslist: [],
      // 
      buyCount: 1,
      // tab
      tabIndex: 0,
      // 
      pageIndex: 1,
      // 
      pageSize: 10,
      // 
      comments: [],
      // 
      totalcount: 0,
      // 
      comment: "",
      images: {
        // required
        normal_size: [
          // { id: "unique id", url: "image url" },
          // { id: "unique id", url: "image url" }
        ]
      },
      //  
      zoomerOptions: {
        zoomFactor: 4,
        pane: "container-round",
        hoverDelay: 300,
        namespace: "inline-zoomer",
        move_by_click: true,
        scroll_items: 5,
        choosed_thumb_border_color: "#bbdefb"
      }
    };
  },
  // 
  methods: {
    // 
    initData() {
      // 
      this.buyCount = 1;
      // 
      this.artID = this.$route.params.id;
      // //console.log(this.artID);

      //   axios
      this.$axios
        .get(
          `http://111.230.232.110:8899/site/goods/getgoodsinfo/${this.artID}`
        )
        .then(result => {
          // handle success
          // //console.log(result);
          // 
          this.hotgoodslist = result.data.message.hotgoodslist;
          // 
          this.goodsinfo = result.data.message.goodsinfo;
          // 
          this.imglist = result.data.message.imglist;
          // 
          this.images.normal_size = [];
          // 
          this.imglist.forEach(v=>{
            this.images.normal_size.push({
              id:v.id,
              url:v.original_path
            })
          });
        });
      // 
      this.getComments();
    },
    // 
    getComments(pageIndex) {
      //   axios
      this.$axios
        .get(
          `http://111.230.232.110:8899/site/comment/getbypage/goods/${
            this.artID
          }?pageIndex=${this.pageIndex}&pageSize=${this.pageSize}`
        )
        .then(result => {
          // handle success
          // //console.log(result);
          this.comments = result.data.message;
          this.totalcount = result.data.totalcount;
        });
    },
    // 
    pageChange(pageIndex) {
      // //console.log(pageIndex);
      this.pageIndex = pageIndex;
      this.getComments();
    },
    // 
    submitComment() {
      //
      if (this.comment == "") {
        // 
        this.$Message.warning("!");
        // return;
      } else {
        //   axios
        this.$axios
          .post(`site/validate/comment/post/goods/${this.artID}`, {
            commenttxt: this.comment
          })
          .then(result => {
            //console.log(result);
            // 
            if (result.data.status == 0) {
              // 
              this.$Message.success(result.data.message);
              // 
              this.comment = "";
              // 1
              this.pageIndex = 1;
              // 
              this.getComments();
            } else {
            }
          });
      }
    },
    // 
    add2Cart() {
      // 
      this.$store.commit('add2Cart',{
        // id
        goodId:this.artID,
        // 
        goodNum:this.buyCount
      });
      this.$notify({
          title: 'success',
          message: '',
          type: 'success',
          duration:1000
        });
    }
  },
  // 
  created() {
    //console.log(this.$route.params);
    this.initData();
  },
  // 
  watch: {
    $route(newVal, oldVal) {
      //  
      this.images.normal_size = [];
      // 
       this.initData();
    }
  }
};
</script>

<style>
.tab-content img {
  display: block;
  width: 100%;
}
.inline-zoomer-zoomer-box {
  width: 395px;
}
.thumb-list img{
    width: 160px;
    height: 160px;
}
</style>