<template>
  <div class="right-auto orderDetail">
    <div
      class="bg-wrap"
      style="min-height: 765px;"
    >
      <div class="sub-tit">
        <a
          href="javascript:void(0)"
          class="add"
          @click="$router.go(-1)"
        >
          <i class="iconfont icon-reply" ></i></a>
        <ul>
          <li class="selected">
            <a href="javascript:;"></a>
          </li>
        </ul>
      </div>
      <div class="order-progress">
        <ul>
          <li
            :class="{active:orderinfo.status>=1}"
            class="first active"
          >
            <div class="progress"></div>
            <div class="info">{{orderinfo.add_time  | shortTimePlus}}</div>
          </li>
          <li
            :class="{active:orderinfo.status>=2}"
            class=""
          >
            <div class="progress"></div>
            <div class="info">{{orderinfo.confirm_time  | shortTimePlus}}</div>
          </li>
          <li
            :class="{active:orderinfo.status>=2}"
            class=""
          >
            <div class="progress"></div>
            <div class="info">{{orderinfo.express_time  | shortTimePlus}}</div>
          </li>
          <li
            :class="{active:orderinfo.status>=4}"
            class="last"
          >
            <div class="progress"></div>
            <div class="info">{{orderinfo.complete_time  | shortTimePlus}}</div>
          </li>
        </ul>
      </div>
      <div class="form-box accept-box form-box1">
        <dl class="head form-group">
          <dd>
            {{orderinfo.order_no}}

            <!-- <a
              href="#/site/goods/payment/12"
              class="btn-pay"
            ></a> -->
            <!---->
            <router-link
              @click="receiveGoods"
              v-show="orderinfo.status==1"
              class="btn-pay"
              :to="`/payMoney/`+orderinfo.id"
            ></router-link>
            <a
              v-show="orderinfo.status==2||orderinfo.status==3"
              @click="receiveGoods"
              class="btn-pay"
            >
              
            </a>
            <a
              v-show="orderinfo.status==4"
              class="btn-pay"
            >
              
            </a>
          </dd>
        </dl>
        <dl class="form-group">
          <dt></dt>
          <dd>
            {{ orderinfo.statusName}}
          </dd>
        </dl>
        <dl class="form-group">
          <dt></dt>
          <dd>
            BD20181202191935391
          </dd>
        </dl>
        <dl class="form-group">
          <dt></dt>
          <dd>{{orderinfo.paymentTitle}}</dd>
        </dl>
      </div>
      <div class="table-wrap">
        <table
          width="100%"
          border="0"
          cellspacing="0"
          cellpadding="5"
          class="ftable"
        >
          <tbody>
            <tr>
              <th align="left"></th>
              <th width="60%"></th>
              <th width="10%">
              </th>
              <th width="10%"></th>
              <th width="10%"></th>
            </tr>
            <tr
              v-for="(item, index) in goodsList"
              :key="item.goods_id"
            >
              <td width="60">
                <img
                  :src="item.imgurl"
                  class="img"
                >
              </td>
              <td align="left">
                <!-- <a
                  target="_blank"
                  href="/goods/show-92.html"
                >{{item.goods_title}}</a> -->
              <router-link :to="`/detail/`+item.goods_id">{{item.goods_title}}</router-link>
              </td>
              <td align="center">
                <s>{{item.goods_price}}</s>
                <p>{{item.real_price}}</p>
              </td>
              <td align="center">{{item.quantity}}</td>
              <td align="center">{{item.real_price*item.quantity}}</td>
            </tr>
            <tr>
              <td
                colspan="7"
                align="right"
              >
                <p>
                  <b class="red">{{orderinfo.payable_amount}}</b>&nbsp;&nbsp;+&nbsp;&nbsp;
                  <b class="red">{{orderinfo.express_fee}}</b>
                </p>
                <p style="font-size: 22px;">
                  <b class="red">{{orderinfo.order_amount}}</b>
                </p>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="form-box accept-box">
        <dl class="head form-group">
          <dd></dd>
        </dl>
        <dl class="form-group">
          <dt></dt>
          <dd>{{orderinfo.accept_name}}</dd>
        </dl>
        <dl class="form-group">
          <dt></dt>
          <dd>{{orderinfo.area+orderinfo.address}}</dd>
        </dl>
        <dl class="form-group">
          <dt></dt>
          <dd>{{orderinfo.mobile}} </dd>
        </dl>
        <dl class="form-group">
          <dt></dt>
          <dd>{{orderinfo.email}}</dd>
        </dl>
        <dl class="form-group">
          <dt></dt>
          <dd>{{orderinfo.message}}</dd>
        </dl>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "orderDetail",
  data: function() {
    return {
      orderId: 0,
      goodsList: [],
      orderinfo: {}
    };
  },
  methods: {
    receiveGoods() {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          //   
          this.$axios
            .get(`site/validate/order/complate/${this.orderId}`)
            .then(result => {
              // //console.log(result);
              if (result.data.status === 0) {
                this.$Message.success(result.data.message);
              }
              this.getOrderInfo();
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    getOrderInfo() {
      this.$axios
        .get(`site/validate/order/getorderdetial/${this.orderId}`)
        .then(result => {
          //console.log(result);
          this.goodsList = result.data.message.goodslist;
          this.orderinfo = result.data.message.orderinfo;
        });
    }
  },
  created() {
    // //console.log(this.$route);
    this.orderId = this.$route.params.orderId;
    this.getOrderInfo();
  }
};
</script>
<style>
</style>
 
        