import Vue from "vue";
import App from "./App.vue";

Vue.config.productionTip = false;

// ProductZoomer 
import ProductZoomer from "vue-product-zoomer";
Vue.use(ProductZoomer);

// jqmain.js 
import $ from "jquery";
Vue.prototype.$$ = $;

// iview
import iView from "iview";
import "iview/dist/styles/iview.css";
// vue
Vue.use(iView);

// element-ui
import ElementUI from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
// vue
Vue.use(ElementUI);

// axios
// vue-resource this.$http
import axios from "axios";
// vue vue
// vue$
Vue.prototype.$axios = axios;

/* // cookie */
 axios.defaults.withCredentials=true;//ajaxcookie 

// axios
axios.defaults.baseURL = "http://111.230.232.110:8899/";

// 
import VueRouter from "vue-router";
// Vue.use(VueRouter)
Vue.use(VueRouter);
//  
import "./assets/statics/site/css/style.css";

//  
import index from "./components/index.vue";
import detail from "./components/02.detail.vue";
import shopCart from "./components/03.shopCart.vue";
import order from "./components/04.order.vue";
import login from "./components/05.login.vue";
import payMoney from "./components/06.payMoney.vue";
import paySuccess from "./components/07.paySuccess.vue";
import userCenter from "./components/08.userCenter.vue";
import orderList from "./components/09.orderList.vue";
import orderDetail from "./components/10.orderDetail.vue";
import orderIndex from "./components/11.orderIndex.vue";

//  
//  "component" 
//  Vue.extend() 
// 
// 
const routes = [
  {
    path: "/",
    // /index
    redirect: "/index"
  },
  {
    path: "/index",
    component: index
  },
  {
    path: "/detail/:id",
    component: detail
  },
  {
    path: "/shopCart",
    component: shopCart
  },
  {
    path: "/order/:ids",
    component: order,
    meta: { checkLogin:true }
  },
  {
    path: "/login",
    component: login
  },
  {
    path: "/paySuccess",
    component: paySuccess,
    meta: { checkLogin:true }
  },
  {
    path: "/payMoney/:orderId",
    component: payMoney,
    meta: { checkLogin:true }
  },
  {
    path: "/userCenter",
    component:userCenter,
    meta: { checkLogin:true },
    children: [
      {
        path:'',
        redirect:'orderIndex'
      },
      {
        path:'orderIndex',
        component:orderIndex
      },
      {
        path:'orderList',
        component:orderList
      },
      {
        path:'orderDetail/:orderId',
        component:orderDetail
      }
    ]
  }
];

// 
let router = new VueRouter({
  routes,
  // 
  //   
  scrollBehavior(to, from, savedPosition) {
    // return 
    return { x: 0, y: 0 }
  },
  // #
  mode: 'history'
})

//  
router.beforeEach((to, from, next) => {
  // //console.log('');
  // //console.log(to);
  // //console.log(from);
  // if (to.path.indexOf('/order')!=-1) {
    if (to.meta.checkLogin==true) {
    // 
    // 
    axios.get("site/account/islogin").then(result => {
      //   //console.log(result);
      if (result.data.code == "nologin") {
        // 
        Vue.prototype.$Message.warning("");
        //  
        router.push("/login");
      } else {
        // 
        next();
      }
    });
  } else {
    // next  
    next();
  }
});


// // 
//    
// router.afterEach((to, from) => {
//   // 
//   window.scrollTo(0,0);
// })

//  
// moment
import moment from "moment";
Vue.filter("shortTime", value => {
  return moment(value).format("YYYY-MM-DD");
});
Vue.filter("shortTimePlus", value => {
  return moment(value).format("YYYY-MM-DD HH:mm:ss");
});

// Vuex
import Vuex from "vuex";
Vue.use(Vuex);
// 
const store = new Vuex.Store({
  // 
  state: {
    count: 0,
    // 

    // let data = JSON.parse(window.localStorage.getItem('hm24'));
    // if (!data) {
    //   90: 10,
    //   80: 6
    // }
    // || false ||
    cartData: JSON.parse(window.localStorage.getItem("hm24")) || {
      90: 10,
      80: 6
    },
    // cartData:data,
    // 
    isLogin: false
  },
  // Vuex
  getters: {
    totalCount(state) {
      // //console.log(state);
      // state
      // 

      // return 888;
      let num = 0;
      for (const key in state.cartData) {
        num += state.cartData[key];
      }
      return num;
    }
  },
  // 
  mutations: {
    // 
    // statestate
    // 
    // increment (state,obj) {
    // //console.log('');
    // //console.log(obj);
    // state.count+=n;
    // state.count+=m;
    // }
    //  2=>to
    //  goodId(id) goodNum()
    add2Cart(state, obj) {
      // //console.log(obj);
      // 
      // 
      if (state.cartData[obj.goodId] != undefined) {
        state.cartData[obj.goodId] += obj.goodNum;
        // 
        // let oldNum = state.cartData[obj.goodId];
        // oldNum += obj.goodNum;
        // state.cartData[obj.goodId] =oldNum;
      } else {
        // 
        // 
        // state.cartData[obj.goodId] = obj.goodNum;
        //  Vue.set
        // 1  2   3 
        Vue.set(state.cartData, obj.goodId, obj.goodNum);
      }
      // 
      // //console.log(state);
    },
    updateCartData(state, obj) {
      // //console.log(obj);
      //  03.shopCart.vue 
      // 
      state.cartData = obj;
    },
    // 
    // watch,Vue.delete
    delGoodsById(state, id) {
      // //console.log(id);
      // idstate
      // delete state.cartData[id];
      // delete  vue
      // 1  2 
      // vue.delete
      Vue.delete(state.cartData, id);
    },
    changeLogin(state, isLogin) {
      state.isLogin = isLogin;
    }
  }
});

// 
window.onbeforeunload = function() {
  window.localStorage.setItem("hm24", JSON.stringify(store.state.cartData));
};
// 
//  router 
// 
new Vue({
  render: h => h(App),
  // 
  router,
  // storeVue store
  store,
  // 
  created() {
    // //console.log('');
    // 
    // 
    axios.get("site/account/islogin").then(result => {
      // //console.log(result);
      if (result.data.code == "nologin") {
        // 
        Vue.prototype.$Message.warning("");
        // ()  
        router.push("/login");
      } else {
        // 
        store.state.isLogin = true;
        // 
        // next();
      }
    });
  }
}).$mount("#app");
